import {Component, HostListener, Input, OnInit} from '@angular/core';
import {MatTableDataSource} from '@angular/material/table';

import {Product} from '../../../models/product.model';
import {BasketProduct} from '../models/basket-product.model';

@Component({
  selector: 'app-pos-product-search',
  templateUrl: './pos-product-search.component.html',
  styleUrls: ['./pos-product-search.component.scss']
})
export class PosProductSearchComponent implements OnInit {
  constructor() {}
  @Input() products: Product[];
  @Input() basketProducts: BasketProduct[];

  displayedColumns: string[] = ['name', 'price'];
  dataSource: MatTableDataSource<Product>;
  filterValue = '';
  @HostListener('document:keypress', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    if (event.key === 'Enter' && this.dataSource.filteredData.length === 1) {
      this.addToBasket(this.dataSource.filteredData[0]);
      this.filterValue = '';
    } else if (event.key === 'Enter') {
      this.filterValue = '';
      this.applyFilter();
    } else {
      this.filterValue = this.filterValue + event.key;
      this.applyFilter();
    }
  }

  @HostListener('document:keydown', ['$event'])
  handleKeyDownEvent(event: KeyboardEvent) {
    if (event.key === 'Backspace') {
      this.deleteOneInput();
      this.applyFilter();
    }
  }

  ngOnInit(): void {
    this.dataSource = new MatTableDataSource(this.products);
  }

  handleVirtualKeyboardEvent(numberValue: number) {
    this.filterValue = this.filterValue + numberValue;
    this.applyFilter();
  }

  clearInput() {
    this.filterValue = '';
    this.applyFilter();
  }

  deleteOneInput() {
    this.filterValue = this.filterValue.slice(0, this.filterValue.length - 1);
    this.applyFilter();
  }

  applyFilter() {
    this.dataSource.filter = this.filterValue.trim().toLowerCase();
  }

  addToBasket(product: Product, amount: number = 1) {
    if (this.basketProducts[0].id !== product.id) {
      this.basketProducts.unshift(new BasketProduct(product, amount));
    } else {
      this.basketProducts[0].amount = this.basketProducts[0].amount + amount;
    }
  }
}
