import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable, of} from 'rxjs';

import {DbSuccessResponse} from '../../food-planner/models/dbSuccessResponse.model';
import {ConfigurationService} from '../../services/configuration.service';
import {Product} from '../models/product.model';
import {WarehouseLocation} from '../models/warehouse-location.model';
import {Warehouse} from '../models/warehouse.dto';

@Injectable({
  providedIn: 'root'
})
export class ProductService {
  constructor(private http: HttpClient, private configuration: ConfigurationService) {}

  getById(productId: string): Observable<Warehouse> {
    return this.http.get<Warehouse>(`${this.configuration.nestJSServerUrl}/warehouse/product/${productId}`);
  }

  getAll(): Observable<Product[]> {
    return of([
      {
        id: 'aaaa',
        name: '1111',
        price: 1
      },
      {
        id: 'bbbb',
        name: '2222',
        price: 2
      },
      {
        id: 'cccc',
        name: '3333',
        price: 3
      },
      {
        id: 'dddd',
        name: '4444',
        price: 4
      },
      {
        id: 'eeee',
        name: '5555',
        price: 5
      },
      {
        id: 'ffff',
        name: '6666',
        price: 6
      },
      {
        id: 'gggg',
        name: '7777',
        price: 7
      }
    ]);
    // return this.http.get<Warehouse[]>(`${this.configuration.nestJSServerUrl}/warehouse/product/all`);
  }

  add(product: WarehouseLocation): Observable<DbSuccessResponse> {
    return this.http.post<DbSuccessResponse>(`${this.configuration.nestJSServerUrl}/warehouse/product`, product);
  }

  update(product: WarehouseLocation): Observable<DbSuccessResponse> {
    return this.http.put<DbSuccessResponse>(`${this.configuration.nestJSServerUrl}/warehouse/product`, product);
  }

  product(product: WarehouseLocation): Observable<DbSuccessResponse> {
    return this.http.put<DbSuccessResponse>(`${this.configuration.nestJSServerUrl}/warehouse/product/delete`, product);
  }
}
