import {Product} from '../../../models/product.model';

export class BasketProduct extends Product {
  id: string;
  name: string;
  price: number;
  // tslint:disable-next-line: variable-name
  _id?: string;
  // tslint:disable-next-line: variable-name
  _rev?: string;

  constructor(product: Product, public amount: number) {
    super();
    Object.assign(this, product);
  }
}
