import {animate, state, style, transition, trigger} from '@angular/animations';
import {Component, DoCheck, Input, ViewChild} from '@angular/core';
import {MatTable} from '@angular/material/table';

import {BasketProduct} from '../models/basket-product.model';

@Component({
  selector: 'app-sale-basket',
  templateUrl: './sale-basket.component.html',
  styleUrls: ['./sale-basket.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)'))
    ])
  ]
})
export class SaleBasketComponent implements DoCheck {
  @Input() basketProducts: BasketProduct[];
  columnsToDisplay = ['name', 'amount', 'price', 'cost'];
  @ViewChild(MatTable) table: MatTable<BasketProduct>;

  ngDoCheck() {
    if (this.table) {
      this.table.renderRows();
    }
  }

  getTotalCost() {
    return this.basketProducts.map(t => t.price).reduce((acc, value) => acc + value, 0);
  }

  getNumberOfItems() {
    return this.basketProducts.map(t => t.amount).reduce((acc, value) => acc + value, 0);
  }
}
