import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';

import {ConfigurationService} from '../../services/configuration.service';
import {DbSuccessResponse} from '../models/dbSuccessResponse.model';
import {WarehouseLocation} from '../models/warehouse-location.model';
import {Warehouse} from '../models/warehouse.dto';

@Injectable({
  providedIn: 'root'
})
export class WarehouseService {
  constructor(private http: HttpClient, private configuration: ConfigurationService) {}

  getById(warehouseName: string): Observable<Warehouse> {
    return this.http.get<Warehouse>(`${this.configuration.nestJSServerUrl}/warehouse/${warehouseName}`);
  }

  getAll(): Observable<Warehouse[]> {
    return this.http.get<Warehouse[]>(`${this.configuration.nestJSServerUrl}/warehouse/all`);
  }

  addLocation(warehouseLocation: WarehouseLocation): Observable<DbSuccessResponse> {
    return this.http.post<DbSuccessResponse>(`${this.configuration.nestJSServerUrl}/warehouse`, warehouseLocation);
  }

  updateLocation(warehouseLocation: WarehouseLocation): Observable<DbSuccessResponse> {
    return this.http.put<DbSuccessResponse>(`${this.configuration.nestJSServerUrl}/warehouse`, warehouseLocation);
  }

  deleteLocation(warehouseLocation: WarehouseLocation): Observable<DbSuccessResponse> {
    return this.http.put<DbSuccessResponse>(
      `${this.configuration.nestJSServerUrl}/warehouse/location/delete`,
      warehouseLocation
    );
  }
}
