import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';

import {WarehouseLocation} from '../../models/warehouse-location.model';
import {Warehouse} from '../../models/warehouse.dto';

@Component({
  selector: 'app-warehouse-manager',
  templateUrl: './warehouse-manager.component.html',
  styleUrls: ['./warehouse-manager.component.scss']
})
export class WarehouseManagerComponent implements OnInit {
  public warehouses: Warehouse[];

  constructor(private route: ActivatedRoute) {}

  ngOnInit(): void {
    this.route.data.subscribe((data: {warehouses: Warehouse[]}) => {
      this.warehouses = data.warehouses;
    });
  }

  getProductsStoredInWarehouses() {
    const locations: WarehouseLocation[] = [].concat.apply([], this.warehouses.map(warehouse => warehouse.locations));
    const products = locations.map(location => {
      const productArray = [];
      if (location && location.productIds) {
        location.productIds.forEach(productId => {
          productArray.push({
            id: productId,
            floor: location.floor,
            layerStart: location.layerStart,
            layerEnd: location.layerEnd,
            rowStart: location.rowStart,
            rowEnd: location.rowEnd,
            columnStart: location.columnStart,
            columnEnd: location.columnEnd,
            warehouseName: location.warehouseName
          });
        });
      }
      if (productArray.length !== 0) {
        return productArray;
      }
    });
    let result = [].concat.apply([], products);
    result = result.filter(product => !(product === null || product === undefined));
    return result;
  }
}
