import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {PosComponent} from './components/pos/pos.component';
import {WarehouseDashboardComponent} from './components/warehouse-dashboard/warehouse-dashboard.component';
import {WarehouseManagerComponent} from './components/warehouse-manager/warehouse-manager.component';
import {WarehouseProductAddComponent} from './components/warehouse-product-add/warehouse-product-add.component';
import {WarehouseProductSearchComponent} from './components/warehouse-product-search/warehouse-product-search.component';
import {ProductsResolver} from './resolvers/products.resolver';
import {WarehouseResolver} from './resolvers/warehouses.resolver';
import {WarehouseComponent} from './warehouse.component';

const routes: Routes = [
  {
    path: '',
    component: WarehouseComponent,

    children: [
      {
        path: '',
        component: WarehouseDashboardComponent
      },
      {
        path: 'add',
        component: WarehouseProductAddComponent
      },
      {
        path: 'search',
        component: WarehouseProductSearchComponent
      },
      {
        path: 'manage',
        component: WarehouseManagerComponent,
        resolve: {
          warehouses: WarehouseResolver
        }
      }
    ]
  },
  {
    path: 'pos',
    component: PosComponent,
    resolve: {
      products: ProductsResolver
    }
  },
  {
    path: '**',
    redirectTo: ''
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WarehouseRoutingModule {}
